import axios from 'axios'
import {dateUtil} from '@/utils/dateUtil'
import {BASE_URL} from './common'
let base = BASE_URL

let AUTH_TOKEN = sessionStorage.getItem('access-user')
axios.defaults.headers.common['Authorization'] = AUTH_TOKEN
/**
 * 根据用户获取用户专题报告 POST /briefing/search
 * @param params
 */
export const getReports = params => {
  return axios.post(`${base}/briefing/search`, params).then(res => {
    let {data} = res
    if (data.content.length > 0) {
      data.content.forEach(function (item) {
        item.createTime = dateUtil.formatDate(new Date(item.dateCreated), 'yyyy/MM/dd')
      })
    }

    return data
  })
}
